.contPainting{
    max-width: 97vw;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: #ffffff;;
}
.boxPainting{
    background-color: #964b00;
    width: 90vw ;
    max-width: 750px;
    max-height: 100%;
    border:#D1BC8A 1px solid;
    border-radius: 15px;
    margin: 15px;
    padding: 5px 5px 15px 0px;
    box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.308) ;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.items__painting{
    display: flex;
    flex-direction:column;
    align-items: center;
}
.painting{
    cursor: pointer;
    min-width: 150px;
    max-height: 100%;
    text-align: center;
    margin: 5px 0;
    padding: 5px 0;
    overflow: hidden;
}
.basket__gallary, .basket__gallaryAnimate {
    color: white;
    margin-top: 7px;
    height: 25px;
}
.basket__gallaryAnimate{
    font-size: 19px;
    transform: scale(0);
    animation: buttonGallery 1.5s 1 forwards;
    
}


.basket__gallaryAnimateActive{
    transform: scale(1);
}
  

@keyframes buttonGallery {
    0%{transform: scale(1); margin-right: 150px;}
    30%{margin-right: 0px;}
    50%{margin-left: 150px;}
    100%{transform: scale(1); margin-left: 0; }
  }


.painting img:hover{
    transform: scale(1);
    transition: 1s;
    background-color: rgba(255, 255, 255, 0.39);
}

.description{
    word-wrap: break-word;
    padding: 5px;
}
.painting img{ 
    background-color: #55504b;
    border: rgb(43, 37, 37) double 25px;
    max-width: 250px;
}

.description__ul{
    text-decoration: none ;
    list-style-type: none ;
    font-size: 17px;
    font-weight: 700;
    color: #ffffff;
}

.buttonGallary {
    color: #fff;
    border: 1px solid #fff;
    border-radius: 15px;
    padding: 7px;
    cursor: pointer;
    opacity: 0;
    animation: buttons 3s infinite forwards;
  }
  .buttonGallaryActive{
    opacity: 1;
  }
  .button:hover {
    background-color: rgb(0, 0, 0);
    transition: 1s;
  }


@media screen and (max-width:750px) {
    .painting img{ 
        max-width: 70%;
    }
}